 /* Extra small devices (phones, 600px and down) */
 @media only screen and (max-width: 600px) {
    #textoSeccion9{
      position: absolute;
      top: 655%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
      /* height: 465px; */
      text-align: center;
      z-index: 1000;
    }
  
    #contenedorTimer {
        position: relative;
        width: 83%;
        left: 50%;
        transform: translate(-50%, 0%);
        z-index: 10000;
        background-color: transparent;
      }
    
    .timerFinalizado {
        position: relative;
        width: 90%;
        left: 50%;
        transform: translate(-50%, 0%);
        z-index: 10000;
        background-color: transparent;
        padding: 5px 0 30px 0;
        font-size: 25px;
    }
  }
  
  
