 /* Extra small devices (phones, 600px and down) */
 @media only screen and (max-width: 600px) {
    #textoRegalo{
      position: absolute;
      top: 457%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
      /* height: 465px; */
      text-align: center;
      z-index: 100;
    }
  }
  
