@media only screen and (max-width: 600px) {

    .scroll-up {
        position: absolute;
        left: 50%;
        top: 31%;
        display: block;
        text-align: center;
        font-size: 20px;
        z-index: 100;
        text-decoration: none;
        text-shadow: 0;
        width: 23px;
        height: 23px;
        border-bottom: 2px solid rgb(124, 123, 123);
        border-right: 2px solid rgb(124, 123, 123);
        z-index: 9;
        left: 48%;
        transform: translate(-50%, 0%) rotate(-45deg);
        animation:         fade_move_up 4s ease-in-out infinite;
      }
     
      .scroll-up2 {
        position: absolute;
        left: 50%;
        top: 33%;
        display: block;
        text-align: center;
        font-size: 20px;
        z-index: 100;
        text-decoration: none;
        text-shadow: 0;
        width: 23px;
        height: 23px;
        border-bottom: 2px solid rgb(124, 123, 123);
        border-right: 2px solid rgb(124, 123, 123);
        z-index: 9;
        left: 48%;
        transform: translate(-50%, 0%) rotate(-45deg);
        animation:         fade_move_up 4s ease-in-out infinite;
      }
    
      .scroll-up3 {
        position: absolute;
        left: 50%;
        top: 35%;
        display: block;
        text-align: center;
        font-size: 20px;
        z-index: 100;
        text-decoration: none;
        text-shadow: 0;
        width: 23px;
        height: 23px;
        border-bottom: 2px solid rgb(124, 123, 123);
        border-right: 2px solid rgb(124, 123, 123);
        z-index: 9;
        left: 48%;
        transform: translate(-50%, 0%) rotate(-45deg);
        animation:         fade_move_up 4s ease-in-out infinite;
      }
    
      /*animated scroll arrow animation*/
      @keyframes fade_move_up {
        0%   { transform:translate(0,10px) rotate(225deg); opacity: 0;  }
        50%  { opacity: 1;  }
        100% { transform:translate(0,-10px) rotate(225deg); opacity: 0; }
      }  
       
      .scroll-down {
        position: absolute;
        left: 50%;
        bottom: 52%;
        display: block;
        text-align: center;
        font-size: 20px;
        z-index: 100;
        text-decoration: none;
        text-shadow: 0;
        width: 23px;
        height: 23px;
        border-bottom: 2px solid rgb(92, 91, 91);
        border-right: 2px solid rgb(92, 91, 91);
        z-index: 9;
        left: 10%;
        transform: translate(-50%, 0%) rotate(45deg);
        animation:         fade_move_up 4s ease-in-out infinite;
      }
    
      /*animated scroll arrow animation*/
      @keyframes fade_move_down {
        0%   { transform:translate(0,-10px) rotate(45deg); opacity: 0;  }
        50%  { opacity: 1;  }
        100% { transform:translate(0,10px) rotate(45deg); opacity: 0; }
      }
    
}

